import { Component, h, Prop, Host, Element, Event, EventEmitter, Method } from '@stencil/core';
import { IFunction } from '../storm-sidenav-collapse/storm-sidenav-collapse';
import PerfectScrollbar from 'perfect-scrollbar';

@Component({
  tag: 'storm-sidenav',
  styleUrl: 'storm-sidenav.css',
  assetsDirs: ['assets'],
  shadow: false,
})
export class StormSideNav {
  @Element() host: HTMLElement;
  @Prop() name: string = '';
  @Prop() image: string = 'logo-ct.png';
  @Prop() basePath: string;
  @Prop() dataSet: IFunction[];
  @Prop({ mutable: true }) value: string;
  @Event() change: EventEmitter<IFunction>;

  private _collapse: HTMLStormSidenavCollapseElement;
  private selectedFunction: IFunction;

  buildClasses(): string {
    return 'sidenav navbar navbar-vertical navbar-expand-xs border-0 border-radius-xl my-3 fixed-start ms-3 bg-gradient-dark ps';
  }

  onChange = (event: CustomEvent<IFunction>) => {
    event.preventDefault();
    this.selectedFunction = event.detail;
    this.value = this.selectedFunction.id;
    this.change.emit(event.detail);
  };

  @Method()
  async getValue(): Promise<IFunction> {
    return this.selectedFunction;
  }

  @Method()
  async setLabel(fun: IFunction): Promise<void> {
    await this._collapse.setLabel(fun);
  }

  componentDidLoad() {
    const container = this.host.querySelector('.ps');
    new PerfectScrollbar(container);
  }

  render() {
    return (
      <Host>
        <aside class={this.buildClasses()}>
          <storm-sidenav-header name={this.name} image={this.image} basePath={this.basePath}></storm-sidenav-header>
          <slot />
          <storm-sidenav-collapse ref={el => (this._collapse = el)} dataSet={this.dataSet} value={this.value} onChange={this.onChange}></storm-sidenav-collapse>
        </aside>
      </Host>
    );
  }
}
